// Hero.js (just update image)
import React, { useEffect, useRef } from "react";
import { StaticImage } from "gatsby-plugin-image";
import { Link } from "@reach/router";
import TalkToUsNew from "@components/common/talk-to-us-new";

const Hero = ({ db }) => {
  const imageRef = useRef(null);
  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          imageRef.current.classList.add("animate");
        } else {
          imageRef.current.classList.remove("animate");
        }
      },
      { threshold: 0.2 }
    ); // trigger when 20% visible

    if (imageRef.current) observer.observe(imageRef.current);
    return () => observer.disconnect();
  }, []);

  return (
    <section className="binance-hero-banner item-center">
      <div className="item-center">
        <h1 className="about-h2">
          Launch Your Next-Gen Crypto Exchange with{" "}
          <span>Binance Clone Script</span>
        </h1>
        <p className="about-p">
          What Is Binance? A Complete Guide to Starting Your Own Bitcoin
          Exchange
        </p>
        <div className="buttons">
          <Link to="/contact-us" className="roll-button roll-btn">
            <span>View Live Demo</span>
          </Link>
          <TalkToUsNew
            db={db}
            label="Free Consultation"
            customClass="normal-roll-button normal-roll-btn"
          />
        </div>
      </div>
      <div className="binance-image" ref={imageRef}>
        <StaticImage
          src="../../styles/assets/images/binance-clone/trade-screen.png"
          alt="trade-screen-image"
          fetchpriority="high"
          layout="constrained"
          quality={75}
          loading="eager"
          placeholder="none"
        />
      </div>
    </section>
  );
};

export default Hero;
