import React, { useState } from 'react'
import { Accordion } from 'react-bootstrap';

const Faqv2 = ({ faqData }) => {
    const [visibleItems, setVisibleItems] = useState(4);
    const [hasMore, setHasMore] = useState(true);

    const handleLoadMore = () => {
        setVisibleItems(visibleItems + 4);
        if (visibleItems + 4 >= faqData?.length) {
            setHasMore(false);
        }
    };
    return (
        <div className="faq_rhs-v2 item-start">
            <h3 className="about-h3">
                Frequently Asked <span>Questions</span>
            </h3>
            <div className="faq_acc_wrapper-v2">
                <Accordion>
                    {faqData?.length > 0 &&
                        faqData?.slice(0, visibleItems)?.map((item, index) => (
                            <Accordion.Item key={index} eventKey={index.toString()}>
                                <Accordion.Header>
                                    <div>{index + 1}.</div>
                                    <div>{item.question}</div>
                                </Accordion.Header>
                                <Accordion.Body>{item.answer}</Accordion.Body>
                            </Accordion.Item>
                        ))}
                </Accordion>
            </div>
            {faqData?.length > 4 && hasMore && (
                <div
                    role="button"
                    tabIndex="0"
                    className="decenter_hero_sec_btn d-flex justify-content-center"
                    onKeyDown={(e) => { }}
                    onClick={handleLoadMore}
                >
                    <button className="roll-button roll-btn" ><span>Load More</span></button>
                </div>
            )}
        </div>
    )
}

export default Faqv2