import React, { useEffect, useRef } from "react";

const capsuleList = [
  { id: 0, text: "Market, Limit, Stop Limit Order" },
  { id: 1, text: "Dynamic Token Adding Tool" },
  { id: 2, text: "Crypto Loans" },
  { id: 3, text: "Derivative Trading" },
  { id: 4, text: "Staking Module" },
  { id: 5, text: "Spot Trading" },
  { id: 6, text: "Liquidity Integration" },
  { id: 7, text: "Real Time Trading view Chart" },
  { id: 8, text: "Core Multi-Sig Wallet Set Up" },
  { id: 9, text: "Fingerprint access" },
  { id: 10, text: "Cold/Offline Wallet Support" },
  { id: 11, text: "Margin Trading" },
  { id: 12, text: "Multi Coins Support" },
  { id: 13, text: "Multi Language Support" },
  { id: 14, text: "Light and Dark mode" },
  { id: 15, text: "Full Source Code Access" },
  { id: 16, text: "3rd Party Wallet Connectivity" },
  { id: 17, text: "Referrals & Commission management" },
];

const BinanceFeature = () => {
  const itemsRef = useRef([]);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add("animate");
          } else {
            entry.target.classList.remove("animate");
          }
        });
      },
      { threshold: 0.2 }
    );

    itemsRef.current.forEach((el) => el && observer.observe(el));

    return () => observer.disconnect();
  }, []);

  return (
    <section className="binance-feature">
      <div className="item-center">
        <h3 className="about-h3">
          Our Top Features of the <span>Binance Clone Script</span>
        </h3>
        <p className="about-p">
          Justtry offers an advanced Binance clone script with many unique
          features that promote a sustainable and scalable trading environment
          for active investors.
        </p>
        <div className="capsule col_flex">
          {capsuleList.map((item, idx) => (
            <span
              key={item.id}
              ref={(el) => (itemsRef.current[idx] = el)}
              className="capsule-item"
            >
              {item.text}
            </span>
          ))}
        </div>
      </div>
    </section>
  );
};

export default BinanceFeature;
